<template>
  <div>
    <div class="row">
      <div class="col-md-11 col-sm-6">
        <h3 class="fw-bold">Edit Reseller</h3>
      </div>
      <div class="col-md-1 col-sm-6 my-2">
        <router-link to="/admin/resellers">
          <v-btn color="error">
            <v-icon left class="mt-1">mdi-format-list-bulleted</v-icon>List</v-btn>
        </router-link>
      </div>
    </div>
    <div class="my-5">
      <v-form @submit.prevent="resellerAddFormSubmit" ref="form" v-model="valid" lazy-validation>
        <v-container>
          <v-row>
            <v-col cols="12" sm="6" md="12">
              <v-text-field 
                  v-model="resellerUser.name"
                  label="Name" 
                  prepend-icon="mdi-account"></v-text-field>
            </v-col>
            <v-col cols="12" sm="6" md="12">
              <v-text-field 
                  v-model="resellerUser.email"
                  label="E-mail"
                  prepend-icon="mdi-email"></v-text-field>
            </v-col>
            <v-col cols="12" sm="6" md="12">
              <v-text-field prepend-icon="mdi-lock" 
                  v-model="resellerUser.password"
                  :append-icon="show1 ? 'mdi-eye' : 'mdi-eye-off'"
                  :passrules="[passrules.required, passrules.min]" :type="show1 ? 'text' : 'password'"
                  name="input-10-1" label="Password" hint="At least 8 characters" counter
                  @click:append="show1 = !show1"></v-text-field>
            </v-col>
            <v-col cols="12" sm="6" md="12">
              <v-select
                  v-model="resellerUser.reseller_gender"
                  :rules="genderRules"
                  label="Select Gender"
                  :items="genders"
                  item-text="gender"
                  item-value="gender"
                  prepend-icon="mdi-human"></v-select>
            </v-col>
            <v-col cols="12" sm="6" md="12">
              <v-text-field
                  v-model="resellerUser.reseller_dob"
                  type="date"
                  label="Date of birth"
                  prepend-icon="mdi-calendar" multiple>
              </v-text-field>
            </v-col>
            <v-col cols="12" sm="6" md="12">
              <v-text-field prepend-icon="mdi-phone" 
                  v-model="resellerUser.mobile_phone" 
                  :counter="10"
                  :error-messages="errors" 
                  label="Phone Number"></v-text-field>
            </v-col>
            <v-col cols="12" sm="6" md="6">
              <v-autocomplete
                  v-model="resellerUser.profession_id"
                  label="Select Profession"
                  :items="allProfessions"
                  item-text="profession"
                  item-value="id"
                  prepend-icon="mdi-tray-arrow-down">
              </v-autocomplete>
            </v-col>
            <v-col cols="12" sm="6" md="6">
              <v-autocomplete 
                  v-model="allAddedSkills" 
                  :items="allSkills" 
                  item-text="skill" 
                  item-value="id"
                  multiple chips  
                  label="Skills & Experience" 
                  prepend-icon="mdi-download"></v-autocomplete>
            </v-col>
            <v-col cols="12" sm="6" md="6">
                <v-autocomplete
                  v-model="resellerUser.state_id"
                  @change="getCities"
                  :items="allstates"
                  label="Select State"
                  item-text="state"
                  item-value="id"
                  prepend-icon="mdi-account">
              </v-autocomplete>
            </v-col>
            <v-col cols="12" sm="6" md="6">
                <v-autocomplete
                  v-model="resellerUser.city_id"
                  :items="cities"
                  label="Select City"
                  item-text="city"
                  item-value="id"
                  prepend-icon="mdi-city" >
                </v-autocomplete>
            </v-col>
            <v-col cols="12" sm="6" md="12">
              <v-textarea 
                  v-model="resellerUser.address"
                  label="Address" 
                  prepend-icon="mdi-download"
                  rows="2"></v-textarea>
            </v-col>
          </v-row>
          <v-col cols="12" md="12" class="py-0 d-flex" style="justify-content: space-between;">
                <div>
                  <v-checkbox v-model="resellerUser.user_status" label="Active" value="1"></v-checkbox>
                </div>
                <div class="ms-3 pb-3">
                    <v-btn v-if="isLoading" class="my-2 me-3 cred">Loading...</v-btn>
                    <v-btn v-else class="my-2 me-3 cred" type="submit" :disabled="!valid">SAVE</v-btn>
                </div>
          </v-col>

        </v-container>
      </v-form>
    </div>
    <v-snackbar v-model="snackbar" :timeout="timeout" type="success" border="left" elevation="3">
      <template v-slot:action="{ attrs }">
        <v-btn color="blue"  v-bind="attrs" text @click="snackbar = false">Close</v-btn>
      </template>{{text}}
    </v-snackbar>
  </div>
</template>

<script>
import axios from "axios";
export default {
    name: "ResellersUpdate",
    props:{
      id:Number
    },
    components: {  },
    data() {
        return {
            isLoading: false,
            valid: false,
            menu2: false,
            show1: false,
            resellerUser: {},
            genders: [{id:1,gender:'Male'},{id:2,gender:'Female'}],
            allstates: [],
            cities: [],
            allProfessions:[],
            allSkills: [],
            addedSkills: [],
            allAddedSkills: [],
            password: '',
            passrules: {
                required: value => !!value || 'Required.',
                min: v => v.length >= 8 || 'Min 8 characters',
            },
            snackbar: false,
            text: '',
            timeout: '',
        }
    },
    mounted() {
      this.getResellerUser();
      this.All_States();
      this.getCities();
      this.All_Professions();
      this.All_skills();
    },
    methods:{
        getResellerUser() {
          axios.get('admin/reseller/single/get/'+this.id)
            .then((resp)=>{
              this.resellerUser = resp.data.resellers;
              this.getCities();
              // added Skills
              this.addedSkills = resp.data.resellers.allskill;
              for (let i = 0; i < this.addedSkills.length; i++) {
                this.allAddedSkills.push(this.addedSkills[i].skill_id);
              }
            });
        },
        All_States(){
          axios.get('allstates')
            .then((resp)=>{
              this.allstates = resp.data.locations;
            });
        },
        getCities() {
          if (this.resellerUser.state_id) {
            axios.get('states/'+this.resellerUser.state_id)
                .then(response => {
                  this.cities = response.data;
                })
          } else {
            this.cities = [];
          }
        },
        All_Professions(){
          axios.get('professions')
            .then((resp)=>{
              this.allProfessions = resp.data.professions;
            });
        },
        All_skills(){
          axios.get('skills')
            .then((resp)=>{
              this.allSkills = resp.data.skills;
            });
        },
      resellerAddFormSubmit() {
        this.isLoading = true;
        const config = {
            headers: { 'content-type': 'multipart/form-data' }
        }
        let data = { 
            user_id: this.resellerUser.id,
            name: this.resellerUser.name,
            reseller_gender: this.resellerUser.reseller_gender,
            email: this.resellerUser.email,
            password: this.resellerUser.password,
            reseller_dob: this.resellerUser.reseller_dob,
            mobile_phone: this.resellerUser.mobile_phone,
            profession_id: this.resellerUser.profession_id,
            skills_id: this.allAddedSkills,
            state_id: this.resellerUser.state_id,
            city_id: this.resellerUser.city_id,
            address: this.resellerUser.address,
            user_status: this.resellerUser.user_status,
        };
        axios.post('admin/reseller/update', data, config)
          .then((resp) => {
              this.snackbar = true;
              this.text = resp.data.message;
              this.timeout = 3000;
              this.isLoading = false;
        });
      }
    }
}
</script>